
.abn-header{
    width: 170px;
    margin: 0 auto;
    height: 85px;
}
.abn-body{
    background-color: #f9f9f9;
    padding: 50px;
}

.main-body-abn{
    background: #f5f5f5;
    border:1px solid #ddd;
    padding: 0px;

}
#header{
    padding: 10px;
    margin: 0px;
    border-bottom:1px solid #ddd;
    color: #005E5D;

}

.abn-body-container{

    padding: 20px;
    /*border: 1px solid #ddd;*/
}

.abn-body-container .form-container{
    padding: 20px;
    background-color: #fff;
}

.how-to-login span{
    background-color: #f5f5f5;
    padding: 10px;
    display: inline-block;
    width: 80px;
    height: 80px;
    border: 1px solid #ddd;
    cursor: pointer;
    transition: 300ms;
}
.how-to-login span.active{
    background: white;
    border: 1px solid #969696;

}
.inputs-container span{
    display: inline-block;
    font-size: 16px;
}
.inputs-container input{
    border: 1px solid black;
    padding: 6px 15px;
    font-size: 16px;

}

.abn-body-container ol{
    padding-left: 15px;
}

.abn-body-container .row{
    margin-bottom: 15px;
}
.abn-body-container input[type="checkbox"]{
    display: none;
}
.account-number-static{
    padding: 6px 15px;
    border:1px solid black;
    border-right-color: transparent;
    background: #f5f5f5;
}

.account-input input{
    border-left-color: transparent;

}
.label-container{
    padding-top: 6px;
}

.handle-checkbox{
    display: inline-block;
    width: 17px;
    height: 17px;
    border: 1px solid #888;
    border-radius: 3px;
    background-color: #fff;
    margin-top: 6px;
    position: relative;
    top: 3px;
    margin-right: 6px;
}

.abn-body-container input[type="checkbox"]:checked + label .handle-checkbox:after{
    content: "";
    display: inline-block;
    position: absolute;
    top: -1px;
    left: -1px;
    width: 17px;
    height: 17px;
    background: url(../../static/media/abn-amro-checked.e9a2b8f4.svg) no-repeat center;
    background-size: 12px 12px;
}

.newcode{
    margin-top: 15px;
    color: #009286;
    text-decoration: underline;
    display: inline-block;

}

.newcode:hover{
    color: #f60;
    text-decoration: underline;
}

.getfeed-btn{
    padding-top: 20px;
}
.react-code-input-container{
    border:1px solid black;
}

.react-code-input{
    padding: 0px 30px;
}

.react-code-input-container input{
    border: none!important;
    width: 20px!important;
    height: 27px!important;
    padding: 1px;
    padding-left: 5px!important;
}

.inputs-container input:focus ,.inputs-container input:active{
    background-color: #ffc;
}

.input-errors-anb{
    color: #f60;
    margin-top: 5px;
}

.code-input input{
    letter-spacing:12px;
}


.code-input input::-webkit-input-placeholder { /* Edge */
    color: #707071;
    font-size:12px;
}

.code-input input::-ms-input-placeholder {
    color: #707071;
    font-size:10px;
}

.code-input input::placeholder {
    color: #707071;
    font-size:10px;
}

.terms-condition{
    margin-bottom: 0px;
}


.bank-submit{
    margin-top: 20px;
    width: 226px;
    font-size: 14px!important;
}

.server-error{
    color:red;
    font-size: 13px;
}



.spinner-container{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #ffffffad;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 1;
    top: 0px;
    left: 0px;
}

.loading-spinner{
    background: white;
    top: 50%;
    left: 50%;
}



/*Dialog*/
.dialog-container-text{
    font-size: 17px;
}

.title-dialog i{
    font-size: 35px;
    color:green;
}
.submit-btn-dialog{
    -webkit-justify-content: center;
            justify-content: center;
}

.submit-btn-dialog button{
    font-size: 12px;
}
/*Dialog*/

.login-ing-container .up-title{
    color: #ff6200;
    letter-spacing: -1px;
    font-weight: 500;
    font-size: 23px;
    margin-bottom: 0px;

}

.login-ing-container{
    display: inline-block;
    width: 400px;
    border: 1px solid #dedede;
    border-radius: 4px;
    text-align: left;

}

.input-ing{
    width: 100%;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    background-color: white !important;
    background-image: none !important;
    color: rgb(0, 0, 0) !important;
}
.input-ing input:-webkit-autofill, .input-ing textarea:-webkit-autofill, .input-ing select:-webkit-autofill {
    background-color: white !important;
    background-image: none !important;
    color: rgb(0, 0, 0) !important;
}
.input-ing input ,.input-ing label{
    font-size: 16px;
}

.input-ing input{
    padding: 6px 10px;
    line-height: 20px;
}

.input-ing.haserror{
    border-bottom:1px solid #ff6200;
}


.input-ing>div::after{
    border-bottom-color: #ff6200;
}


.change-login-type{
    border-bottom: 1px solid #dedede;
    padding: 0px 30px;
}

.warning .alert{
    padding: 0px;
    background-color: #f3f8fc;
    border-radius: 4px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 10px 0px;

}


.warning .alert i:first-child{
    color: #9a9df5;
    font-size: 20px;
}

.warning .alert i:last-child{
    color: #868686;
    font-size: 16px;
    cursor: pointer;
}

.warning .alert i{
    padding: 0px 10px;
}
.warning .alert h2{
    margin:0px;
    font-size: 13px;
    font-weight: 600;

}

.warning .alert span{
    font-size: 13px;
    width: 100%;
}
.ing-sub-body{
    padding: 15px;

}

.change-login-type span{
    cursor: pointer;
    padding: 6px 9px;
    font-weight: 500;
    display: inline-block;
    border-bottom: 3px solid transparent;
}


.change-login-type span.isactive{
    border-bottom: 3px solid #ff6200;
    font-weight: 600;
}

.input-errors-ing {

    color: #525151;

    margin-top: -5px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-size: 11px;

}

.input-errors-ing i{
    color: red;
    margin-right: 4px;
    font-size:14px;
}

